<template>
    <div id="app">
        <div id="head"><div id="head-text">肋片效率计算</div></div>
        <div id="input">
            <div v-for="(v,n) in inputValueOptions" :key="n" class="input-element">
                {{n}} {{v.unit}}
                <div>
                    <el-input-number
                    v-model="inputValues[n]"
                    :precision="v.precision"
                    :step="v.step"
                    :min="v.min"
                    :max="v.max">
                    </el-input-number>
                </div>
            </div>
            <el-button type="primary" icon="el-icon-check" @click="exe" circle></el-button>
        </div>
        <div id="output">
            <el-table :data="output">
                <el-table-column prop="a" label="节点M*N"></el-table-column>
                <el-table-column prop="b" label="Bi"></el-table-column>
                <el-table-column prop="c" label="二维η值"></el-table-column>
                <el-table-column prop="d" label="一维η值"></el-table-column>
                <el-table-column prop="e" label="相对偏差"></el-table-column>
            </el-table>
        </div>
        <div id="chart">
            <div id="chart-panel"></div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            inputValueOptions: {
                t0: {precision: 2, step: 1, min: -273.15, max: 1000, unit: 'ºC'},
                tf: {precision: 2, step: 1, min: -273.15, max: 1000, unit: 'ºC'},
                h:  {precision: 1, step: 1, min: 0.1, max: 1000, unit: 'W/(m*m*K)'},
                λ:  {precision: 1, step: 1, min: 0.1, max: 1000, unit: 'W/(m*K)'},
                δ:  {precision: 2, step: 0.01, min: 0.01, max: 10, unit: 'm'},
                H:  {precision: 2, step: 0.01, min: 0.01, max: 10, unit: 'm'},
                n:  {precision: 0, step: 100, min: 1000, max: 20000, unit: '次'}
            },
            inputValues: {
                t0: 100,
                tf: 20,
                h: 400,
                λ: 8,
                δ: 0.02,
                H: 0.08,
                n: 5000
            },
            output: [
                {a: null, b: null, c: null, d: null, e: null}
            ],
        }
    },
    methods: {
        exe () {
            if(this.t0 <= this.tf) alert('流体温度tf要低于肋端温度t0');
            else this.paint(this.cal());
        },
        cal () {
            let {t0, tf, h, λ: la, δ: de, H, n: num} = this.inputValues;

            let M = H, N = de;
            while(M > 50 || N > 50) M /= 2, N /= 2;
            while(M < 10 || N < 10) M *= 2, N *= 2;
            M = parseInt(M + 0.5) + 1, N = parseInt(N + 0.5) + 1;
            this.output[0].a = `${M}*${N}`;
            this.output[0].b = h * de / la;

            let nodes = [];
            for(let m = 0; m < M; m++) {
                nodes[m] = [];
                for(let n = 0; n < N; n++) {
                    nodes[m][n] = t0 - tf;
                }
            }
            let Bi = h / la * (H / (M - 1) + de / (N - 1)) / 2;
            for(let i = 0; i < num; i++) {
                for(let m = 0; m < M; m++) for(let n = 0; n < N; n++) {
                    if(m == 0);
                    else if(m != 0 && m != M - 1 && n == 0) {
                        nodes[m][n] = (nodes[m-1][n] + nodes[m+1][n] + 2 * nodes[m][n+1]) / 4;
                    }
                    else if(m != 0 && m != M - 1 && n != 0 && n != N - 1) {
                        nodes[m][n] = (nodes[m-1][n] + nodes[m+1][n] + nodes[m][n-1] + nodes[m][n+1]) / 4;
                    }
                    else if(m != 0 && m != M - 1 && n == N - 1){
                        nodes[m][n] = (nodes[m-1][n] + nodes[m+1][n] + 2 * nodes[m][n-1]) / (4 + 2 * Bi);
                    }
                    else if(m == M - 1 && n == 0) {
                        nodes[m][n] = (nodes[m-1][n] + nodes[m][n+1]) / (2 + Bi);
                    }
                    else if(m == M - 1 && n != 0 && n != N - 1) {
                        nodes[m][n] = (nodes[m][n-1] + nodes[m][n+1] + 2 * nodes[m-1][n]) / (4 + 2* Bi);
                    }
                    else if(m == M - 1 && n == N -1) {
                        nodes[m][n] = (nodes[m][n-1] + nodes[m-1][n]) / (2 + 2 * Bi);
                    }
                }
            }

            let sum = 0;
            for(let m = 1; m < M - 1; m++) sum += nodes[m][N-1];
            for(let n = 1; n < N - 2; n++) sum += nodes[M-1][n];
            let eta2 = 0.5 * (nodes[0][N-1] + nodes[M-1][0]) + sum;
            eta2 /= ((M - 2) + (N - 2)) * (t0 - tf);
            this.output[0].c = eta2.toString().slice(0, eta2.toString().indexOf('.') + 5);

            let mH = Math.pow(h / la / de, 1 / 2) * (de + H);
            let eta1 = (Math.exp(mH) - Math.exp(-1 * mH)) / (Math.exp(mH) + Math.exp(-1 * mH));
            eta1 /= mH;
            this.output[0].d = eta1.toString().slice(0, eta1.toString().indexOf('.') + 5);

            let re = (eta1 - eta2) / eta2 * 100;
            this.output[0].e = `${re.toString().slice(0, re.toString().indexOf('.') + 5)}%`;

            return nodes;
        },
        paint (nodes) {
            nodes = nodes.map((n, i) => {
                return n.map((m, j) => [i, j, m]);
            })
            let data = [];
            for(let n of nodes) {
                data = data.concat(n);
            }
            let chart = this.$echarts.init(document.getElementById('chart-panel'));
            chart.setOption({
                visualMap: {
                    show: true,
                    min: 0,
                    max: this.inputValues.t0 - this.inputValues.tf,
                    inRange: {
                        //color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
                        color: ['rgb(0,0,255)', 'rgb(32,0,224)', 'rgb(64,0,192)', 'rgb(96,0,160)', 'rgb(128,0,128)', 'rgb(160,0,96)', 'rgb(192,0,64)', 'rgb(224,0,32)', 'rgb(255,0,0)']
                    }
                },
                xAxis3D: {
                    type: 'value',
                    name: 'M',
                },
                yAxis3D: {
                    type: 'value',
                    name: 'N',
                },
                zAxis3D: {
                    type: 'value',
                    name: 'θ/℃',
                    max: this.t0 - this.tf,
                    min: 0,
                },
                grid3D: {
                    axisLine: {
                        lineStyle: { color: '#fff' }
                    },
                    axisPointer: {
                        lineStyle: { color: '#fff' }
                    },
                },
                series: [{
                    type: 'bar3D',
                    shading: 'lambert',
                    data
                }]
            })
        }
    }
}
</script>

